/* ------------------ Icon preview ( remove it in production sever )--------------------*/
.icon-page i {font-size: 20px; margin-bottom: 10px;}
.icon-page .row {margin-left: 0;margin-right: 0;}

/* ------------------ Target all icons --------------------*/
[class^="im-"],
[class*=" im-"],
[class^="br-"],
[class*=" br-"],
[class^="ec-"],
[class*=" ec-"],
[class^="st-"],
[class*=" st-"],
[class^="me-"],
[class*=" me-"],
[class^="en-"],
[class*=" en-"],
[class^="fa-"],
[class*=" fa-"] {
    display: inline-block;
    vertical-align: middle;
    margin-top: -1px;
}


