body {
    overflow-x: hidden;
    min-height: 100%;
    background: #ffffff;
    color: #768399;
    -webkit-font-smoothing: antialiased;
    font-weight: 400;
    transition: -webkit-transform 0.4s ease !important;
    transition: transform 0.4s ease !important;
    position: relative;
}
body.stop-scrolling {
    height: 100%;
    overflow: hidden;
    -webkit-filter: blur(6px);
    -moz-filter: blur(6px);
    -ms-filter: blur(6px);
    -o-filter: blur(6px);
    filter: blur(6px);
}
body.remove-scroll {
    height: 100%;
    overflow: hidden;
}
body.login-page {
    background: #75b9e6;
}
body.login-page #login {
    margin: 10% auto 0 auto;
    position: relative;
    width: 420px;
}
body.login-page #login #logo {
    position: absolute;
    top: -75px;
    left: 50%;
    margin-left: -105px;
    /* width of the logo devided by 2 */
}
body.login-page #login .login-wrapper {
    width: 100%;
    height: auto;
    background-color: #ffffff;
    border-radius: 4px;
    display: inline-block;
    box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.1);
}
body.login-page #login .login-wrapper .nav-tabs.nav-justified li:first-child a {
    border-top-left-radius: 4px;
}
body.login-page #login .login-wrapper .nav-tabs.nav-justified li:last-child a {
    border-top-right-radius: 4px;
}
body.login-page #login .login-wrapper .nav-tabs.nav-justified li a {
    border: none;
}
body.login-page #login .login-wrapper #user-info {
    position: relative;
    height: 156px;
}
body.login-page #login .login-wrapper #user-info img.cover {
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
}
body.login-page #login .login-wrapper #user-info h4.headline {
    float: left;
    padding: 10px 30px;
}
body.error-page {
    margin-top: 5%;
    background-color: #f3f5f6;
}
body.error-page h1.error-number {
    text-align: center;
    font-size: 20em;
    font-family: 'Cantarell';
}
body.mce-fullscreen #header {
    display: none;
}
body.full-screen {
    width: 100%;
}
body.full-screen #header {
    left: 0;
}
* {
    box-sizing: border-box;
