}
/* -----------------------------------------
   Header styles
----------------------------------------- */
#header {
    height: 50px;
    background: #75b9e6;
    z-index: 999;
    position: absolute;
    width: 100%;
    top: 0;
}
#header .container-fluid {
    padding-left: 0;
    padding-right: 0;
}
#header.header-fixed {
    position: fixed;
}
#header .navbar {
    height: 50px;
    margin-bottom: 0;
    padding-right: 20px;
    background: #75b9e6;
    border-radius: 0;
    border: none;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
}
#header .navbar .navbar-toggle {
    margin-right: 20px;
    padding: 9px 10px;
    margin-top: 8px;
    margin-bottom: 8px;
    border-radius: 4px;
    position: absolute;
    right: 0;
}
#header .navbar .navbar-toggle .icon-bar {
    display: block;
    width: 22px;
    height: 2px;
    border-radius: 1px;
    background: #ffffff;
}
#header .navbar .navbar-collapse {
    max-height: 340px;
    overflow-x: visible;
    padding-right: 15px;
    padding-left: 15px;
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.1);
    background-color: #75b9e6;
}
#header .navbar .navbar-brand {
    padding: 0;
    width: 210px;
    margin-right: 0;
}
#header .navbar .navbar-brand .text-logo-element {
    font-size: 32px;
    color: #1a608f;
    margin-left: 34px;
    margin-right: 10px;
    margin-top: -8px;
}
#header .navbar .navbar-brand .text-logo,
#header .navbar .navbar-brand .text-slogan {
    font-weight: bold;
    font-family: 'Droid Sans';
    font-size: 24px;
    text-transform: uppercase;
    margin-top: 15px;
    display: inline-block;
}
#header .navbar .navbar-brand .text-logo {
    color: #124363;
}
#header .navbar .navbar-brand .text-slogan {
    color: #ffffff;
}
