    line-height: 50px;
    padding-top: 0;
    padding-bottom: 0;
    color: #ffffff;
    width: auto;
    position: relative;
    transition: all;
    transition-duration: 0.4s;
    overflow-x: hidden;
    font-size: 13px;
    min-width: 50px;
