----------------------------------------- */
/* ------------------ Landing intro --------------------*/
.landing-intro {
    margin: 0;
    position: relative;
    //background: url(../../../../public/img/cup-mug-desk-office.jpg);
    background-size: cover !important;
    /*Header*/
    /*Intro slogan*/
}
.landing-intro .header {
    padding: 15px 0;
    border-bottom: 1px solid #f3f5f6;
    /*Logo*/
    /*Navigation*/
}
.landing-intro .header .logo:before,
.landing-intro .header .logo:after {
    content: " ";
    display: table;
}
.landing-intro .header .logo:after {
    clear: both;
}
.landing-intro .header .logo img {
    float: left;
}
.landing-intro .header .logo .slogan {
    color: #ffffff;
    font-weight: bold;
    float: left;
    border-left: 1px solid #f3f5f6;
    padding-left: 20px;
    margin-top: 5px;
    margin-bottom: 0;
}
.landing-intro .header nav {
    float: right;
    list-style: none;
    padding: 8px 0;
}
.landing-intro .header nav li {
    display: inline-block;
    margin-right: 10px;
}
.landing-intro .header nav li:last-child {
    margin-right: 0;
}
.landing-intro .intro-slogan {
    text-shadow: 0 1px 5px rgba(0, 1, 0, 0.3);
    color: #ffffff;
}
.landing-intro .intro-slogan h1 {
    text-transform: uppercase;
    font-size: 42px;
    margin-bottom: 25px;
    margin-top: 35px;
}
.landing-intro .intro-slogan h1 strong {
    text-decoration: underline;
}
.landing-intro .intro-slogan p {
    font-size: 24px;
}
.landing-intro .description-bar {
    background: rgba(0, 0, 0, 0.35);
    height: 180px;
}
.landing-intro .description-bar p {
    padding: 0 25px;
    font-weight: normal;
    font-size: 24px;
    color: #ffffff;
    line-height: 40px;
    display: table-cell;
    height: 145px;
    vertical-align: middle;
}
/* ------------------ Scroll to next section --------------------*/
.scrollTo {
    display: block;
    text-decoration: none;
    position: absolute;
    bottom: 0;
    left: 50%;
    margin-left: -28px;
    /*bouncearrow animation*/
}
.scrollTo img {
    animation: bouncearrow 1s infinite;
    -moz-animation: bouncearrow 1s infinite;
    /* Firefox */
    -webkit-animation: bouncearrow 1s infinite;
    /* Safari and Chrome */
    -o-animation: bouncearrow 1s infinite;
    /* Opera */
}
@keyframes bouncearrow {
    from {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
    to {
        -webkit-transform: translateY(-20px);
        transform: translateY(-20px);
    }
}
@-webkit-keyframes bouncearrow {
    /* Safari and Chrome */
    from {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
    to {
        -webkit-transform: translateY(-20px);
        transform: translateY(-20px);
    }
}
/* ------------------ Headers --------------------*/
h3.section-header {
    font-weight: bold;
    font-size: 24px;
    color: #69768d;
    line-height: 1;
    margin: 15px 0 15px;
}
h3.section-header-1 {
    display: inline-block;
    font-size: 20px;
    text-transform: uppercase;
    letter-spacing: 1px;
    text-align: center;
    border-bottom: 1px solid #f3f5f6;
    padding: 0 0 15px;
    margin: 20px 0 50px;
}
/* ------------------ Hide elements before animations --------------------*/
.logos,
.clean-code .col-md-8,
.clean-code .col-md-4,
.charts .col-md-8,
.charts .col-md-4,
.email-app .col-md-8,
.email-app .col-md-4 {
    opacity: 0;
}
/* ------------------ Section features --------------------*/
.section-features {
    /*Features row*/
}
.section-features .features-row {
    margin-top: 0;
    padding: 20px 0;
}
/* ------------------ Testimonials --------------------*/
.testimonials {
    padding: 25px 0;
    position: relative;
}
.testimonials .testimonials-block {
    transition: all;
    transition-duration: 0.4s;
}
.testimonials .testimonials-block .testimonials-text {
    width: 100%;
    height: auto;
    padding: 20px;
    background: #ffffff;
    border-radius: 4px;
    font-family: Georgia, "Times New Roman", Times, serif;
    font-style: italic;
    position: relative;
    line-height: 28px;
    font-size: 16px;
}
.testimonials .testimonials-block .testimonials-text:after {
    top: 100%;
    left: 20%;
    border: solid transparent;
    content: "";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
    border-color: rgba(255, 255, 255, 0);
    border-top-color: #ffffff;
    border-width: 15px;
    margin-left: -15px;
}
.testimonials .testimonials-block .testimonials-client {
    margin-top: 30px;
    margin-left: 10px;
}
.testimonials .testimonials-block .testimonials-client img {
    float: left;
    border-radius: 50%;
    width: 64px;
    height: 64px;
}
.testimonials .testimonials-block .testimonials-client p {
    padding-left: 80px;
    padding-top: 13px;
}
/* ------------------ Give a try --------------------*/
.give-a-try {
    padding: 60px 15px;
    color: #ffffff;
    text-align: center;
    text-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
    background-image: url(../img/landing/footer-cover.jpg);
    background-size: cover;
}
.give-a-try h1 {
    font-weight: normal;
    font-size: 48px;
}
.give-a-try p {
    font-size: 32px;
}
/* ------------------ Footer --------------------*/
#footer .footer-links {
    text-align: center;
}
#footer .footer-links a {
    text-align: center;
    padding: 0 15px;
    display: inline-block;
    padding-bottom: 15px;
}
/* -----------------------------------------
   Responsive media queries for landing page
----------------------------------------- */
@media all and (max-width: 990px) {
    .landing-intro .header .logo img {
        float: none;
        margin-bottom: 10px;
    }
    .landing-intro .header nav {
        float: none;
        text-align: center;
    }
    .testimonials .testimonials-block {
        margin-bottom: 25px;
    }
}
@media all and (max-width: 660px) {
    .landing-intro .description-bar {
        height: 200px;
    }
    .logos img {
        margin-bottom: 25px;
    }
}
@media all and (max-width: 480px) {
    .landing-intro .description-bar {
        font-size: 18px;
        line-height: 30px;
    }
    .landing-intro .header nav li {
        margin-bottom: 10px;
    }
}
