  left: 0;
  right: 0;
  bottom: 0; }

.sp-alpha-enabled .sp-top {
  margin-bottom: 18px; }

.sp-alpha-enabled .sp-alpha {
  display: block; }

.sp-alpha-handle {
  position: absolute;
