/* -----------------------------------------
   Pace loader
----------------------------------------- */
.pace .pace-progress {
    background: #ffffff;
    position: fixed;
    z-index: 99999;
    top: 0;
    left: 0;
    height: 2px;
    transition: width 1s;
}
.pace-inactive {
    display: none;
}
