        content: "";
    }
}
/* General styles for individual controls. */
.vjs-default-skin .vjs-control {
    outline: none;
    position: relative;
    float: left;
    text-align: center;
    margin: 0;
    padding: 0;
    height: 3.0em;
    width: 4em;
}
.vjs-default-skin .vjs-control div {
